<template>
  <div>
    <el-input v-model="path" placeholder="请输入目录" />
    <el-button @click="search_file_list" :loading="load">获取目录下所有文件列表</el-button>
    <el-button @click="start" :loading="load">开始</el-button>
  </div>
  <el-row>
    <el-col :span="12">
      <div>
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="name" label="文件名" />
        </el-table>
      </div>
    </el-col>
    <el-col :span="12">
      <div>
      </div>
      <el-form :model="form" label-width="120px">
        <el-form-item label="当前状态">
          <div>{{form.status}}</div>
        </el-form-item>
        <el-form-item label="文件名">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="新建目录">
          <el-input v-model="form.dir" />
        </el-form-item>
        <el-form-item label="简介">
          <el-input :rows="6" v-model="form.desc" type="textarea" />
        </el-form-item>
      </el-form>
      <div class="demo-image__placeholder">
        <div class="block">
          <span class="demonstration">封面图</span>
          <el-image fit="scale-down" :src="poster">
            <template #placeholder>
              <div class="image-slot">Loading<span class="dot">...</span></div>
            </template>
          </el-image>
        </div>
        <div class="block">
          <span class="demonstration">背景图</span>
          <el-image fit="contain" :src="backdrop">
            <template #placeholder>
              <div class="image-slot">Loading<span class="dot">...</span></div>
            </template>
          </el-image>
        </div>
      </div>
    </el-col>
  </el-row>

  <div>
    <el-dialog v-model="centerDialogVisible" title="654" width="80%" center>
      <template #title>
        <span>{{ title }}</span>
      </template>
      <div>
        <el-form label-width="120px">
          <el-form-item label="关键词">
            <el-input v-model="keyword" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
        <el-table :data="fileData" border style="width: 100%" @row-dblclick="dblclick">
          <el-table-column label="封面" width="150" >
            <template #default="scope">
              <div style="display: flex; align-items: center">
                <el-image :src="scope.row.image" lazy/>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="文件名" width="200" />
          <el-table-column prop="overview" label="简介"  />
        </el-table>
      </div>
      <template #footer>

      </template>
    </el-dialog>
  </div>
</template>

<script>
import {reactive,toRefs,getCurrentInstance} from "vue";
import {ElMessage,ElMessageBox } from "element-plus";

export default {
  setup(){
    const {proxy} = getCurrentInstance();
    const data = reactive({
      poster:'',
      backdrop:'',
      src:'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
      path:'Z:\\下载区\\[桜都字幕组]2018年年度合集',
      input:false,
      centerDialogVisible:false,
      title:'987',
      keyword:'',
      thisKey:0,
      load:false,
      form:{
        status:'未开始……',
        name:'',
        dir:'',
        desc:'',
      },
      tableData:[
        {
          name: 'Tom',
          path: 'No. 189, Grove St, Los Angeles',
        },
      ],
      fileData:[
        {
          name:''
        }
      ],
      download:{
        name: '',
        dir:'',
        info:'',
        poster:'',
        fanart:''
      }
    })
    const dblclick = async (row, column, event) => {
      data.centerDialogVisible = false;
      let file_info = data.tableData[data.thisKey]
      let id = row.id
      let length = data.tableData.length;
      data.form.status = '['+(data.thisKey+1)+']['+length+']开始搜索['+id+']影视信息';
      const tv_res = await new proxy.$request('http://127.0.0.1:8787/index/find_tv',{id:id}).post();
      data.form.name = data.title;
      data.form.dir = row.name;
      data.form.desc = row.overview;
      if (tv_res.data.code == 200){
        data.form.status = '['+(data.thisKey+1)+']['+length+']开始创建['+tv_res.data.data.name+']目录';
        mkdir(tv_res.data.data.name,data.path);
        data.form.status = '['+(data.thisKey+1)+']['+length+']开始添加指定ID['+tv_res.data.data.id+']到nfo文件';
        insert_id(tv_res.data.data.id,tv_res.data.data.name,data.path)
        if (tv_res.data.data.poster_path != null){
          data.form.status = '['+(data.thisKey+1)+']['+length+']开始下载封面图';
          download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.poster_path,tv_res.data.data.name,data.path,'poster.jpg')
        }
        if (tv_res.data.data.backdrop_path != null){
          data.form.status = '['+(data.thisKey+1)+']['+length+']开始下载背景图';
          download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.backdrop_path,tv_res.data.data.name,data.path,'fanart.jpg')
        }
        data.form.status = '['+(data.thisKey+1)+']['+length+']开始移动文件到输出目录';
        move_file(tv_res.data.data.name,file_info.path_name,data.path,file_info.name);
        data.form.status = '延迟3S'
        // wait(3000)
        setTimeout(function () {
          search_file((data.thisKey+1),data.tableData.length)
          data.form.status = '延迟3S结束'
        },3000);
        // data.form.status = '延迟3S结束'
        // search_file((data.thisKey+1),data.tableData.length)
      }
    }
    const onSubmit =  async () => {
      let file_info = data.tableData[data.thisKey]
      data.centerDialogVisible = false;
      const res = await new proxy.$request('http://127.0.0.1:8787/index/search_file', {name:data.keyword}).post();
      if (res.data.code == 200){
        for (let item of res.data.data.results){
          item.image = 'https://image.tmdb.org/t/p/original'+item.poster_path;
        }
        let length = data.tableData.length;
        data.fileData = res.data.data.results
        data.form.status = '['+(data.thisKey+1)+']['+length+']搜索到['+res.data.data.total_results+']条结果';
        if (res.data.data.total_results == 1){
          data.form.status = '['+(data.thisKey+1)+']['+length+']开始搜索['+res.data.data.results[0]['id']+']影视信息';
          const tv_res = await new proxy.$request('http://127.0.0.1:8787/index/find_tv',{id:res.data.data.results[0]['id']}).post();
          data.form.name = data.title;
          data.form.dir = res.data.data.results[0]['name'];
          data.form.desc = res.data.data.results[0]['overview'];
          if (tv_res.data.code == 200){
            data.form.status = '['+(data.thisKey+1)+']['+length+']开始创建['+tv_res.data.data.name+']目录';
            mkdir(tv_res.data.data.name,data.path);
            data.form.status = '['+(data.thisKey+1)+']['+length+']开始添加指定ID['+tv_res.data.data.id+']到nfo文件';
            insert_id(tv_res.data.data.id,tv_res.data.data.name,data.path)
            if (tv_res.data.data.poster_path != null){
              data.form.status = '['+(data.thisKey+1)+']['+length+']开始下载封面';
              await download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.poster_path,tv_res.data.data.name,data.path,'poster.jpg')
            }
            if (tv_res.data.data.backdrop_path != null){
              data.form.status = '['+(data.thisKey+1)+']['+length+']开始下载背景图';
              await download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.backdrop_path,tv_res.data.data.name,data.path,'fanart.jpg')
            }
            data.form.status = '['+(data.thisKey+1)+']['+length+']开始移动文件到输出目录';
            await move_file(tv_res.data.data.name,file_info.path_name,data.path,file_info.name);
            data.form.status = '延迟3S'
            // wait(3000)
            setTimeout(function () {
              search_file((data.thisKey+1),data.tableData.length)
              data.form.status = '延迟3S结束'
            },3000);
              // data.form.status = '延迟3S结束'
              // search_file((data.thisKey+1),data.tableData.length)
          }
        }else {
          data.centerDialogVisible = true;
        }
      }
    }
    const search_file_list = async () => {
      const res = await new proxy.$request('http://127.0.0.1:8787/index/get_file_list', {path:data.path}).post();
      data.tableData = res.data;
    }
    const start = () => {
      data.load=true
      search_file(0,data.tableData.length)
    }
    const search_file = async (key,length) => {
      if (key >= length){
        data.load = false;
        return false;
      }
      data.form.status = '['+(key+1)+']['+length+']当前正在搜索文件'+data.tableData[key]['name'];
      data.form.name = '搜索中……';
      data.form.dir = '搜索中……';
      data.form.desc = '搜索中……';
      data.poster = 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg';
      data.backdrop = 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg';
      console.log('['+(key+1)+']['+length+']当前正在搜索文件'+data.tableData[key]['name'])
      data.thisKey = key;
      data.title = data.tableData[key]['name'];
      let file_info = data.tableData[key]
      let keywords = file_info.name.replace(/\[.+\]/,'')
      let keyword_arr = keywords.split(' ').filter(function (item){
        return item && item.trim();
      })
      data.keyword = keyword_arr[0]
      const res = await new proxy.$request('http://127.0.0.1:8787/index/search_file', {name:keyword_arr[0]}).post();
      if (res.data.code == 200){
        for (let item of res.data.data.results){
          item.image = 'https://image.tmdb.org/t/p/original'+item.poster_path;
        }
        data.form.status = '['+(key+1)+']['+length+']搜索到['+res.data.data.total_results+']条结果';
        data.fileData = res.data.data.results
        if (res.data.data.total_results == 1){
          data.form.status = '['+(key+1)+']['+length+']开始搜索['+res.data.data.results[0]['id']+']影视信息';
          const tv_res = await new proxy.$request('http://127.0.0.1:8787/index/find_tv',{id:res.data.data.results[0]['id']}).post();
          data.form.name = data.title;
          data.form.dir = res.data.data.results[0]['name'];
          data.form.desc = res.data.data.results[0]['overview'];
          if (tv_res.data.code == 200){
            data.form.status = '['+(key+1)+']['+length+']开始创建['+tv_res.data.data.name+']目录';
            await mkdir(tv_res.data.data.name,data.path);
            data.form.status = '['+(key+1)+']['+length+']开始添加指定ID['+tv_res.data.data.id+']到nfo文件';
            await insert_id(tv_res.data.data.id,tv_res.data.data.name,data.path)
            if (tv_res.data.data.poster_path != null){
              data.form.status = '['+(key+1)+']['+length+']开始下载封面';
              await download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.poster_path,tv_res.data.data.name,data.path,'poster.jpg')
            }
            if (tv_res.data.data.backdrop_path != null){
              data.form.status = '['+(key+1)+']['+length+']开始下载背景图';
              await download_image(tv_res.data.data.id,'https://image.tmdb.org/t/p/original'+tv_res.data.data.backdrop_path,tv_res.data.data.name,data.path,'fanart.jpg')
            }
            data.form.status = '['+(key+1)+']['+length+']开始移动文件到输出目录';
            await move_file(tv_res.data.data.name,file_info.path_name,data.path,file_info.name);
            data.form.status = '延迟3S'
            // wait(3000)
            setTimeout(function () {
              search_file((key+1),data.tableData.length)
              data.form.status = '延迟3S结束'
            },3000);
            // data.form.status = '延迟3S结束'
            // search_file((data.thisKey+1),data.tableData.length)
          }
        }else {
          data.centerDialogVisible = true;
        }
      }
    }
    const wait = async (time) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve();
        }, time);
      });
    }
    const move_file = async (name,path,save_path,rel_name) => {
      const res = await new proxy.$request('http://127.0.0.1:8787/index/move_file',{name:name,path:path,save_path:save_path,rel_name:rel_name}).post();
    }
    const download_image = async (id,url,name,path,save_name) => {
      const res = await new proxy.$request('http://127.0.0.1:8787/index/download_image',{id:id,url:url,name:name,path:path,save_name:save_name}).post();
      if (res.data.code == 200){
        if (save_name == 'poster.jpg'){
          data.poster = res.data.image;
          data.download.poster = '<div>封面图</div><img width="150" src="'+res.data.image+'">'
        }else if (save_name == 'fanart.jpg'){
          data.backdrop = res.data.image
          data.download.fanart = '<div>背景图</div><img width="300" src="'+res.data.image+'">'
        }
      }
    }
    const insert_id = async(id,name,path) => {
      const res = await new proxy.$request('http://127.0.0.1:8787/index/insert_tv_id',{id:id,name:name,path:path}).post();
    }
    const mkdir = async (name,path) => {
      const res = await new proxy.$request('http://127.0.0.1:8787/index/mkdir',{name:name,path:path}).post();
    }
    return {...toRefs(data),start,onSubmit,dblclick,search_file_list}
  },
  name: "index"
}
</script>

<style scoped>
.demo-image__placeholder .block {
  padding: 30px 0;
  text-align: center;
  border-right: solid 1px var(--el-border-color);
  display: inline-block;
  width: 49%;
  box-sizing: border-box;
  vertical-align: top;
}
.demo-image__placeholder .demonstration {
  display: block;
  color: var(--el-text-color-secondary);
  font-size: 14px;
  margin-bottom: 20px;
}
.demo-image__placeholder .el-image {
  padding: 0 5px;
  max-width: 300px;
}

.demo-image__placeholder.image-slot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: var(--el-fill-color-light);
  color: var(--el-text-color-secondary);
  font-size: 14px;
}
.demo-image__placeholder .dot {
  animation: dot 2s infinite steps(3, start);
  overflow: hidden;
}
</style>
